$(document)
    .ready(function () {

        // fix menu when passed
        // $('.masthead')
        //     .visibility({
        //         once: false,
        //         onBottomPassed: function () {
        //             $('.fixed.menu').transition('fade in');
        //         },
        //         onBottomPassedReverse: function () {
        //             $('.fixed.menu').transition('fade out');
        //         }
        //     })
        // ;

        // create sidebar and attach to menu open
        $('.ui.sidebar')
            .sidebar('attach events', '.toc.item')
        ;
        var $followMenu = $('#followMenu');
        var $menu = $followMenu.find('div.menu');
        var $anchors = $menu.find('a');
        var scrolling = false;
        var handler = {
            scrollTo: function (event) {
                var
                    id = $(this).attr('href').replace('#', ''),
                    $element = $('#' + id),
                    position = $element.offset().top - 50
                    ;

                $(this).siblings().removeClass('active');
                $(this).addClass('active');

                scrolling = true;
                $('html, body')
                    .animate({
                        scrollTop: position
                    }, 500, 'swing', function(){
                        scrolling = false;
                    })
                ;
                //location.hash = '#' + id;
                event.stopImmediatePropagation();
                event.preventDefault();
                return false;
            },
            activate: {
                section: function() {
                    if (scrolling) return;

                    var $element = $(this);
                    $anchors.removeClass('active').filter(function(){
                        return $(this).attr('href') === `#${$element.find('a').attr('id')}`
                    }).addClass('active');
                },
                previous: function() {
                    if (scrolling) return;

                    var $prev = $(this).prevAll('h2').first();
                    var $element = $prev.length > 0 ? $prev : $(this);
                    $anchors.removeClass('active').filter(function(){
                        return $(this).attr('href') === `#${$element.find('a').attr('id')}`
                    }).addClass('active');
                }
            }
        };


        // $followMenu
        //     .accordion({
        //         exclusive: false,
        //         animateChildren: false,
        //         onChange: function () {
        //             $('.ui.sticky').sticky('refresh');
        //         }
        //     })
        //     // .find('.menu a[href], .title[href]')
        //     // .on('click', handler.scrollTo)
        // ;

        $followMenu.find('a[href]')
            .on('click', handler.scrollTo);

        if (window.location.hash) {
            console.log(window.location.hash)
            $followMenu.find(`a[href="${window.location.hash}"]`).click();
            history.replaceState("", document.title, window.location.pathname);
        }

        $followMenu.find('.ui.sticky')
            .sticky({
                context: '.main.ui.container'
            })
        ;

        $('.main.ui.container h2.header')
            .visibility({
                observeChanges: false,
                once: false,
                offset: 50,
                onTopPassed: handler.activate.section,
                onTopPassedReverse: handler.activate.previous
            })
        ;

        $('.ui.accordion.faq')
            .accordion()
        ;

        $('.ui.accordion.faq a[href]').on('click', function(e){
            e.stopPropagation();
        });
    })
;

